  mounted: function mounted() {
    this.hot = this.$refs.hotTableComponent.hotInstance;

    if (this.hookFunc) {
      this.addHook(this.hookFunc);
    }
  },
  methods: {
    test: function test() {
      console.log(this.hot.getDataAtCol(0), this.hot.getDataAtRow(0));
    },
    importData: function importData() {
      var _this = this;

      XSheet.importFile(function (tableData, tableName) {
        _this.loadData(tableData);
      });
    },
    transData: function transData() {
      // 
      var data = this.getData();
      var newData = [];
      var maxRow = 0;

      for (var i = 0; i < data.length; i++) {
        if (data[i].length > maxRow) {
          maxRow = data[i].length;
        }
      }

      for (var _i = 0; _i < maxRow; _i++) {
        var temp = [];

        for (var j = 0; j < data.length; j++) {
          temp.push(data[j][_i]);
        }

        newData.push(temp);
      }

      this.loadData(newData);
    },
    render: function render() {
      this.hot.render();
    },
    addHook: function addHook(callback) {
      var _this2 = this;

      // 
      var hooks = ['afterChange', 'afterCreateRow', 'afterCreateCol', 'afterRemoveRow', 'afterRemoveCol', 'afterColumnMove', 'afterRowMove'];
      hooks.forEach(function (hk) {
        _this2.hot.addHook(hk, function () {
          callback(_this2.getData(), _this2.item);
        });
      });
    },
    loadData: function loadData(newData) {
      this.hot.loadData(!newData || newData.length < 1 ? [[]] : this.cloneArray(newData)); // lodaData  [][[]]
    },
    getData: function getData(data) {
      //  data 
      if (!data) {
        data = this.hot.getData();
      }

      var trueRows = -1,
          trueCols = -1;

      for (var i = data.length - 1; i >= 0; i--) {
        for (var j = data[i].length - 1; j >= 0; j--) {
          if (data[i][j] && data[i][j] != null && data[i][j] != '') {
            if (trueRows === -1) {
              trueRows = i;
            }

            if (trueCols < j) {
              trueCols = j;
            }

            break;
          }
        }
      }

      return trueRows === -1 ? [[]] : this.hot.getData(trueRows, trueCols);
    },
    cloneArray: function cloneArray(data) {
      var newData = [];

      for (var i = 0; i < data.length; i++) {
        var temp = [];

        for (var j = 0; j < data[i].length; j++) {
          temp.push(data[i][j]);
        }

        newData.push(temp);
      }

      return newData;
    }
  }
