import "core-js/modules/es.function.name.js";
import HTable from '@/components/general/HTable.vue';
import XSheet from '@/assets/script/x-sheet';
export default {
  components: {
    HTable: HTable
  },
  props: {
    addTab: {
      type: Function
    },
    checkNewLoad: {
      type: Function
    }
  },
  data: function data() {
    return {
      tableId: '',
      tableName: '',
      tableData: [],
      autoSave: undefined,
      saveTip: ''
    };
  },
  watch: {
    tableData: function tableData(newData, oldData) {
      this.$refs.table.loadData(newData);
    }
  },
  activated: function activated() {
    var _this = this;

    // tabtabkeep-alive
    this.checkNewLoad(this.$route.params.tabkey, function (flag, callback) {
      if (flag) {
        _this.$API.getTable({
          _id: _this.$route.params.tabkey
        }, function (message) {
          _this.tableId = _this.$route.params.tabkey;

          if (message.success) {
            callback({
