            });
            _this.tableName = message.info.name;
            _this.tableData = message.info.data;
          }
        });

        _this.saveTip = '';
      }
    });
    this.autoSave = setInterval(function () {
      // 1
      _this.saveTableData();
    }, 1000 * 60);
  },
  deactivated: function deactivated() {
    this.saveTableData();
    clearInterval(this.autoSave);
  },
  methods: {
    downloadFile: function downloadFile() {
      // TODO 
      XSheet.downloadFile(this.$refs.table.getData(), 'out.xlsx');
    },
    saveTableData: function saveTableData(isHand) {
      var _this2 = this;

      //  tabledata 
      if (!this.$refs.table) return;
      this.$API.saveTable({
        _id: this.tableId,
        name: this.tableName,
        data: this.$refs.table.getData()
      }, function (message) {
        console.log(message);

        if (isHand) {
          _this2.saveTip = '';
        } else {
          _this2.saveTip = new Date().toLocaleTimeString('chinese', {
            hour12: false,
            hour: '2-digit',
            minute: '2-digit'
          }) + ' ';
        }
      });
    },
    importData: function importData() {
      var _this3 = this;

      XSheet.importFile(function (tableData, tableName) {
